import React, { useCallback } from 'react';
import { QUIXX_DEFAULT_DATA, QuixxField } from './quixx-data';
import { Icon } from '../../components/icons/icon';
import { QuixxStore, useQuixxSub } from './quixx-store';
import { Util } from '../../util/util';
import { __ } from 'first-intl';

type QuixxFieldProps = {
    field: QuixxField;
    crossed: boolean;
    fieldIdx: number;
    onClick: (field: number) => void;
    deactivated: boolean;
    closed: boolean;
};

const QuixxFieldView: React.FC<QuixxFieldProps> = ({
    field: { value, color },
    crossed,
    onClick,
    fieldIdx,
    deactivated,
    closed,
}) => {
    const onClickWrapped = useCallback(() => onClick(fieldIdx), [onClick, fieldIdx]);
    return (
        <div
            className={Util.classNames('quixx__grid__field', closed && !deactivated && 'quixx__grid__field--closed')}
            style={{ backgroundColor: color }}
            onClick={deactivated ? undefined : onClickWrapped}>
            {value === -1 ? <Icon.Unlock /> : value}
            {crossed && <div className="quixx__grid__field--crossed"></div>}
        </div>
    );
};

type QuixxRowProps = { row: QuixxField[]; rowIdx: number };

const QuixxRow: React.FC<QuixxRowProps> = ({ row, rowIdx }) => {
    const { rowCrossed, rowClosed } = useQuixxSub(
        ({ crossed, closed }) => ({ rowCrossed: crossed[rowIdx], rowClosed: closed[rowIdx] }),
        [rowIdx]
    );
    const onClick = useCallback((field: number) => QuixxStore.onCross(rowIdx, field), [rowIdx]);
    const lastCrossedIdx = rowCrossed.lastIndexOf(true);
    return (
        <>
            {row.map((field, index) => (
                <QuixxFieldView
                    key={field.value}
                    field={field}
                    crossed={rowCrossed[index]}
                    onClick={onClick}
                    fieldIdx={index}
                    deactivated={lastCrossedIdx >= index}
                    closed={rowClosed}
                />
            ))}
        </>
    );
};

const POSSIBLE_BAD_THROWS = 3;

export const QuixxStarted: React.FC = () => {
    const { sum, badThrows } = useQuixxSub(({ badThrows, crossed }) => ({
        badThrows,
        sum: QuixxStore.sum(crossed, badThrows),
    }));
    return (
        <>
            <div className="quixx__grid">
                {QUIXX_DEFAULT_DATA.map((row, index) => (
                    <QuixxRow key={index} row={row} rowIdx={index} />
                ))}
            </div>
            <div className="quixx__info">
                <div className="quixx__info__bad-throws">
                    <div>{__('QUIXX.badThrows')}</div>
                    {Util.array(POSSIBLE_BAD_THROWS).map((_, index) => (
                        <span key={index} onClick={badThrows === index ? QuixxStore.onBadThrow : undefined}>
                            {badThrows > index ? '' : ''}
                        </span>
                    ))}
                </div>
                <div className="quixx__info__sum">
                    <div>{__('QUIXX.points')}</div>
                    <span>{sum}</span>
                </div>
            </div>
        </>
    );
};
