import { Persistore } from 'persistore';
import { createStore } from 'react-use-sub';
import { Util } from '../../util/util';

type QuixxState = {
    badThrows: number;
    crossed: boolean[][];
    closed: boolean[];
};

const initialQuixxState: QuixxState = {
    badThrows: 0,
    crossed: [Util.array(12, false), Util.array(12, false), Util.array(12, false), Util.array(12, false)],
    closed: Util.array(4, false),
};

const _key = 'quixx';

const getHistory = (): QuixxState[] => {
    try {
        const states = Persistore.get(_key);
        return JSON.parse(states as any);
    } catch (e) {
        return [];
    }
};

const get = (): QuixxState => getHistory()[0] || initialQuixxState;

const [useQuixxSub, store] = createStore<QuixxState>(get());

export { useQuixxSub };

const set: typeof store['set'] = (updates) => {
    store.set(updates);
    const updatedState = store.get();

    const history = getHistory();
    history.unshift(updatedState);
    if (history.length > 20) history.pop();
    Persistore.set(_key, JSON.stringify(history));
};

const restart = (): void => {
    set(initialQuixxState);
};

const undo = (): void => {
    const history = getHistory();
    if (history.length > 1) {
        history.shift();
        Persistore.set(_key, JSON.stringify(history));
        const prevState = history[0];
        store.set(prevState);
    }
};

const onBadThrow = () => set(({ badThrows }) => ({ badThrows: badThrows + 1 }));

const POINTS_BY_CROSSES = [0, 1, 3, 6, 10, 15, 21, 28, 36, 45, 55, 66, 78];
const BAD_THROW_DEDUCTION = 5;

const sum = (crossed: QuixxState['crossed'], badThrows: number) =>
    Util.sum(...crossed.map((row) => POINTS_BY_CROSSES[row.filter(Boolean).length])) - badThrows * BAD_THROW_DEDUCTION;

const _setCross = (row: number, field: number) =>
    set(({ crossed }) => ({ crossed: Util.nextArray(crossed, row, Util.nextArray(crossed[row], field, true)) }));

const onCross = (row: number, field: number) => {
    const { crossed, closed } = store.get();
    const rowCrossed = crossed[row];
    if (rowCrossed && !closed[row]) {
        const lastCrossedIdx = rowCrossed.lastIndexOf(true);
        if (lastCrossedIdx < field) {
            if (field === rowCrossed.length - 2) {
                // clicked on the last field and would close the row
                if (rowCrossed.filter(Boolean).length >= 5) {
                    // at least 5 crosses were already made in this row
                    _setCross(row, field);
                    // get the last cross
                    _setCross(row, field + 1);
                }
            } else if (field === rowCrossed.length - 1) {
                // closed the row by clicking the last element without any number
                set({ closed: Util.nextArray(closed, row, true) });
            } else {
                // usual click
                _setCross(row, field);
            }
        }
    }
};

export const QuixxStore = {
    restart,
    undo,
    onBadThrow,
    sum,
    onCross,
};
