import React, { useCallback, useState } from 'react';
import { ClickableDescription } from '../SpadeIsTrump/ClickableDescription';
import { GimmickRow, GimmickStore, useGimmickSub } from './gimmick-store';
import { ValueSelection } from './ValueSelection';
import { Util } from '../../util/util';
import { __string } from 'first-intl';
import { GimmickRowView } from './GimmickRowView';

export const GimmickStarted: React.FC = () => {
    const [selection, setSelection] = useState<GimmickRow | undefined>();
    const closeSelection = useCallback(() => setSelection(undefined), []);
    const players = useGimmickSub(({ players }) => players);
    const activePlayer = GimmickStore.getCurrentActivePlayer(players);
    const firstPartRows = Object.values(GimmickRow).slice(0, 6);
    const secondPartRows = Object.values(GimmickRow).slice(6);
    const winner = useGimmickSub(({ players }) =>
        GimmickStore.allFinished(players) ? GimmickStore.playerWithMostPoints(players) : undefined
    );

    return (
        <>
            <table>
                <thead>
                    <tr>
                        <th className="first" />
                        {players.map(({ name, shortcut, uuid }, playerIdx) => (
                            <ClickableDescription
                                desc={name}
                                short={<span>{shortcut}</span>}
                                key={playerIdx}
                                tag="th"
                                className={Util.classNames(
                                    !winner && activePlayer.uuid === uuid && 'active',
                                    winner?.uuid === uuid && 'winner'
                                )}
                            />
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {firstPartRows.map((row) => (
                        <GimmickRowView
                            key={row}
                            row={row}
                            players={players}
                            onClick={setSelection}
                            activePlayer={activePlayer}
                        />
                    ))}
                    <tr>
                        <ClickableDescription desc={__string('GIMMICK.bonus')} short="" tag="td" />
                        {players.map((player) => (
                            <td key={player.uuid}>
                                {GimmickStore.finishedMultiplier(player.points)
                                    ? GimmickStore.calculateBonus(player.points) || ''
                                    : ''}
                            </td>
                        ))}
                    </tr>
                    {secondPartRows.map((row) => (
                        <GimmickRowView
                            key={row}
                            row={row}
                            players={players}
                            onClick={setSelection}
                            activePlayer={activePlayer}
                        />
                    ))}
                    <tr>
                        <ClickableDescription desc={__string('GIMMICK.sum')} short="" tag="td" />
                        {players.map((player) => (
                            <td key={player.uuid}>{GimmickStore.calculateSum(player)}</td>
                        ))}
                    </tr>
                </tbody>
            </table>
            {selection && <ValueSelection gimmickRow={selection} close={closeSelection} />}
        </>
    );
};
