import React, { ReactNode } from 'react';
import { GimmickRow, PlayerState } from './gimmick-store';
import { Util } from '../../util/util';
import { Cube } from '../Again/Cube';
import { __string } from 'first-intl';

const GIMMICK_NODES: { [key in GimmickRow]: ReactNode } = {
    [GimmickRow.M1]: <Cube count={1} />,
    [GimmickRow.M2]: <Cube count={2} />,
    [GimmickRow.M3]: <Cube count={3} />,
    [GimmickRow.M4]: <Cube count={4} />,
    [GimmickRow.M5]: <Cube count={5} />,
    [GimmickRow.M6]: <Cube count={6} />,
    [GimmickRow.TRIPLE]: __string('GIMMICK.triple'),
    [GimmickRow.FOURSOME]: __string('GIMMICK.foursome'),
    [GimmickRow.FULL_HOUSE]: __string('GIMMICK.fullHouse'),
    [GimmickRow.SMALL_STREET]: __string('GIMMICK.smallStreet'),
    [GimmickRow.BIG_STREET]: __string('GIMMICK.bigStreet'),
    [GimmickRow.SENSATION]: __string('GIMMICK.sensation'),
    [GimmickRow.CHANCE]: __string('GIMMICK.chance'),
};

export type GimmickRowViewProps = {
    row: GimmickRow;
    players: PlayerState[];
    activePlayer: PlayerState;
    onClick: (row: GimmickRow) => void;
};

export const GimmickRowView: React.FC<GimmickRowViewProps> = ({ row, players, activePlayer, onClick }) => (
    <tr>
        <td className="first">{GIMMICK_NODES[row]}</td>
        {players.map(({ points, uuid }) => (
            <td
                className={Util.classNames(activePlayer.uuid === uuid || 'inactive')}
                key={uuid}
                // eslint-disable-next-line
                onClick={points[row] === undefined && activePlayer.uuid === uuid ? () => onClick(row) : undefined}>
                {points[row] === 0 ? '' : points[row]}
            </td>
        ))}
    </tr>
);
